import styled, { css } from "styled-components";
import { LazyLoadImage } from "react-lazy-load-image-component";
import { InnerWrap, Text, Title2, Title3 } from "../../assets/styles/component";

const bg = css`
  content: "";
  z-index: -1;
  position: absolute;
`;

const STechnology = styled.section`
  padding: 300px 0;
  padding-top: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  overflow: hidden;

  & > div {
    & > p {
      text-align: center;
      margin-bottom: 120px;
    }
  }

  @media (max-width: 1100px) {
    padding-top: 100px;
    padding-bottom: 200px;
  }

  background: radial-gradient(
      18.11% 27.45% at -3.27% 46.35%,
      rgba(241, 2, 117, 0.486) 0%,
      rgba(241, 2, 117, 0) 100%
    ),
    radial-gradient(
      25.13% 35.02% at -7.6% 42.58%,
      rgba(2, 2, 241, 0.6) 0%,
      rgba(2, 2, 241, 0) 100%
    ),
    radial-gradient(
      10.66% 15.21% at -2.98% 35.84%,
      rgba(255, 66, 158, 0.6) 0%,
      rgba(255, 66, 158, 0) 100%
    ),
    radial-gradient(
      31.05% 37.8% at 105.64% 123.19%,
      rgba(241, 2, 117, 0.486) 0%,
      rgba(241, 2, 117, 0) 100%
    ),
    radial-gradient(
      37.15% 53% at 102.93% 114.38%,
      rgba(255, 66, 158, 0.6) 0%,
      rgba(255, 66, 158, 0) 100%
    ),
    radial-gradient(
      25.05% 37.35% at 87.59% 93.26%,
      rgba(2, 2, 241, 0.6) 0%,
      rgba(2, 2, 241, 0) 100%
    );
`;

const Title = styled(Title2)`
  text-align: center;
  margin-bottom: 30px;
`;

const TechnologyList = styled.div`
  display: flex;
  justify-content: center;
  gap: 144px 77px;
  flex-wrap: wrap;

  @media (max-width: 1200px) {
    gap: 80px 33px;
  }
`;

const Tech = styled.div`
  width: calc(33% - 77px);
  min-width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;

  p {
    margin-top: 25px;
    text-align: center;
    opacity: 0.7;

    @media (max-width: 1000px) {
      font-size: 15px !important;
    }
  }

  &:last-child {
    img {
      transform: translateY(-15%);
    }
  }
`;

const ImgBox = styled.div`
  height: 272px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  margin-bottom: 50px;

  @media (max-width: 1200px) {
    height: 180px;
    margin-bottom: 0px;
  }

  img {
    max-width: 110%;
    max-height: 120%;
  }
`;

const Technology = ({ language, technologies }) => {
  return (
    <STechnology>
      <InnerWrap>
        <Title>
          Технологии <br /> дополненной реальности
        </Title>

        <Text size="20">
          Объединенные технологии AR и AI позволяют создавать
          <br />
          уникальный контент прямо на трансляции стримера
        </Text>

        <TechnologyList>
          {technologies.map((technology, index) => (
            <Tech key={index}>
              <ImgBox>
                <LazyLoadImage
                  src={technology.img}
                  alt="technology"
                  loading="lazy"
                />
              </ImgBox>
              <Title3>{technology.title}</Title3>
              <Text size="16">{technology.text}</Text>
            </Tech>
          ))}
        </TechnologyList>
      </InnerWrap>
    </STechnology>
  );
};

export default Technology;
